.main-navigation {
	clear: both;
	display: block;
	float: left;
	width: 100%;
}

.main-navigation ul {
	list-style: none;
	margin: 0;
	padding-left: 0;
}

.main-navigation li {
	float: left;
	position: relative;
}

.main-navigation a {
	display: block;
	text-decoration: none;
	font-size: 1em;
	&[rel="home"]{
		border-bottom: none;
	}
}

.main-navigation ul ul {
	box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
	float: left;
	position: absolute;
	top: 1.5em;
	left: -999em;
	z-index: 99999;
}

.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}

.main-navigation ul ul a {
	width: 200px;
}

.main-navigation ul ul li {

}

.main-navigation li:hover > a,
.main-navigation li.focus > a {
}

.main-navigation ul ul :hover > a,
.main-navigation ul ul .focus > a {
}

.main-navigation ul ul a:hover,
.main-navigation ul ul a.focus {
}

.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}

.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li.focus > ul {
	left: 100%;
}

.main-navigation .current_page_item > a,
.main-navigation .current-menu-item > a,
.main-navigation .current_page_ancestor > a,
.main-navigation .current-menu-ancestor > a {
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 37.5em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
	overflow: hidden;
}

/*  POST NAVIGATION */

body:not(.home){
	.nav-links{
		display: flex;
		flex-direction: row;
	}
	.nav-previous, .nav-next{
		padding: 1em;
		flex-grow: 1;
		max-width: 50%;
		text-align: center;

		a{
			color: white;
			padding: 1rem;
			border: none;
		}
		
	}
	.nav-previous{
		background: #333;
		border-bottom: .5em solid lighten(#333, 10%);
		&:hover{
			background: lighten(#333, 10%);
		}
		&:before{
			content: '<<';
			color: white;
		}
	}
	.nav-next{
		background: #1d8aae;	
		border-bottom: .5em solid lighten(#1d8aae, 10%);
		&:hover{
			background: lighten(#1d8aae, 10%);
		}
		&:after{
			content: '>>';
			color: white;
		}
	}
}
